.loader-searchcan {
    position: fixed;
    width: 100%;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(82, 78, 78, 0.7);
    z-index: 9999;
  }
  
  @-webkit-keyframes spin {
    from {
      -webkit-transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
    }
  }
  
  @keyframes spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  .loader-searchcan::after {
    content: "";
    display: block;
    position: absolute;
    left: 50%;
    top: 40%;
    width: 120px;
    height: 120px;
    /* border-style: solid;
    border-color: black;
    border-top-color: transparent; */
    border: 6px solid #f3f3f3; /* Light grey */
    border-top: 6px solid #007bff; /* Blue */
    border-radius: 50%;
    border-width: 4px;
    border-radius: 50%;
    -webkit-animation: spin 0.8s linear infinite;
    animation: spin 0.8s linear infinite;
  }

  @media (max-width: 480px) {
    .loader-searchcan::after {
        left: 40%;
        top: 40%;
        display: block;
        position: absolute;
        width: 120px;
        height: 120px;
        /* text-align: center */
        /* margin: 0 auto */
        /* margin-top: -208.5px; 
        margin-left: -375px; */
    }
}
