  -webkit-animation: swipdown 5s forwards;
          animation: swipdown 5s forwards;
}

@-webkit-keyframes swipdown {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-200px);
            transform: translateY(-200px);
  }

  40% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  70% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }

  100% {
    opacity: 0;
    -webkit-transform: translateY(-200px);
            transform: translateY(-200px);
  }
}

@keyframes swipdown {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-200px);
            transform: translateY(-200px);
  }

  40% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  70% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }

  100% {
    opacity: 0;
    -webkit-transform: translateY(-200px);
            transform: translateY(-200px);
  }
}

